@import "variables/variables.scss";
@import "mixins/mixins.scss";

.slider {
  width: 100%;
  min-width: 200px;

  &__before-toggle,
  &__after-toggle {
    display: block;

    padding-bottom: 1px;

    font-size: $basefontSize;
    line-height: 20px;
    text-transform: uppercase;
  }

  &__input {
    appearance: none;
    -webkit-appearance: none;

    margin: auto 19px;
    padding: 6px 4px;
    width: 25%;
    min-width: 84px;
    height: 14px;

    border-radius: 25px;
    border: 1px solid;
    border-color: $colorBorder;

    &::-webkit-slider-thumb {
      appearance: none;
      -webkit-appearance: none;

      width: 50%;
      height: 6px;
      
      border-radius: 25px;

      background-color: $colorBg;
    }
  }

  @media (min-width: $tabletWidth) {
    &__before-toggle,
    &__after-toggle {
      padding-bottom: 5px;

      border-bottom: 1px dotted;
      border-bottom-color: $colorBorderToggles;
  
      font-size: $basefontSize * 1.25;
    }

    &__input {
      margin: auto 23px;
      padding: 0;
      width: 60%;
      height: 7px;

      border: none;
      background-color: $colorInputBg;

      &::-webkit-slider-thumb {
        width: 32px;
        height: 32px;

        border-radius: 50%;
        border: 12px solid;
        border-color: $colorMainBg;
        box-sizing: border-box;
        box-shadow: 0 0 0 1px $colorBorder;
        box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.15);
      }
    }
  }
}


